<template>
    <div id="submit">
        <div class="scloading" v-show='isLoading'><span class="mui-spinner"></span></div>
        <div id='mask' style='display: none;' class='mask' @click='hideMask'></div>
        <header class="mui-bar mui-bar-nav">
            <a class="mui-icon mui-icon-left-nav" href="javascript:history.back(-1)"></a>
            <h1 class="mui-title"></h1>
            <a class="mui-icon"></a>
        </header>
        <div class="mui-content">
            <div class="addressWrap">
                <div class="deliverymode"></div>
                <div class="deliveryChoice">
                    <ul>
                        <li v-bind:class="isExpress?'selected':''" @click='isExpress = true'></li>
                        <li v-bind:class="isExpress?'':'selected'" @click='isExpress = false'></li>
                    </ul>
                </div>
                <div class="submitaddressWrap" v-show='isExpress'>
                    <div class="order-address submit-order-add" v-show='address.addrId'>
                        <a href="javascript:void(0)" @click='selectAddress'>
                                <span class="name">{{address.receiverName}}</span>
                                <span class="phone">{{address.receiverTel}}</span>
                                <address>{{address.cityName +' '+address.countyName+' '+address.provinceName+' '+address.receiverAddr}}</address>
                            </a>
                    </div>
                    <a class="no-address" href="javascript:void(0)" v-show='!address.addrId' @click='addAddress'><i></i></a>
                </div>
                <div class="submitaddressWrap" v-show='!isExpress'>
                    <p class="orderSince"></p>
                </div>
            </div>
            <div class="orderList">
                <ul>
                    <li>
                        <div class="t">
                            <p></p>
                        </div>
                        <div class="itemsWrap">
                            <div class="items">
                                <div class="img" v-bind:style="'background-image: url('+picUrl+');'"></div>
                                <div class="info">
                                    <h3>{{product.productName}}</h3>
                                    <p class="specifications" v-for='(sku,sIndex) in product.skuKeys'>{{sku.keyName}}{{sku.skuValues[skuIndexs[sIndex]].skuValueName}}</p>
                                    <p class="price"><span>x{{product.buyNum}}</span><em>{{product.defaultPrice}}</em></p>
                                </div>
                            </div>
                            <!-- <div class="items">
                                    <div class="img" style="background-image: url(../images/pic.jpg);"></div>
                                    <div class="info">
                                        <h3> 2013FREE 5.0 536840-003 YK</h3>
                                        <p class="specifications">50ML</p>
                                        <p class="price"><span>x1</span><em>1290.00</em></p>
                                    </div>
                                </div> -->
                        </div>
                        <p class="ft">{{product.buyNum}}<span class="public-color">{{totalAmt | money}}</span></p>
                    </li>
                </ul>
            </div>
            <div class="expresstext" v-show='isExpress'><span>{{address.expressFee || 0}}</span>
                <p></p>
            </div>
            <div class="restform">
                <ul>
                    <li>
                        <div class="hd">:</div>
                        <div class="bd">
                            <div class="info">
                                <div class="input-wrap">
                                    <input v-model='remark' type="text" placeholder="">
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="fbbwrap">
                <div class="ftbtnbar">
                    <div class="content-wrap">
                        <div class="content-wrap-in content-cartwrap-in">
                            <div class="r"><span class="public-color">{{totalAmtWithExpress | money}}</span></div>
                        </div>
                    </div>
                    <div class="button-wrap">
                        <a href="javascript:void(0)" class="button" :class="((!isExpress || address.addrId) && product.productStatusCd === 1)?'':'disabled'" @click='toPay($event)'></a>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <transition name="slide-fade">
            <div id="methodpayment1" v-show='isShowMask' class="actionsheet-spec" style="display:block">
                <div class="close" @click='hideMask'></div>
                <div class="prod-info">
                    <div class="tit"></div>
                </div>
                <ul class="tbviewlist">
                    <li @click='payment'>
                        <a href="javascript:void(0)" class="itemlink">
                            <div class="r public-color">{{totalAmtWithExpress | money}}</div>
                            <div class="c"></div>
                        </a>
                    </li>
                </ul>
                <div class="fbbwrap nofixed">
                    <div class="ftbtnbar">
                        <div class="button-wrap button-wrap-expand">
                            <a href="javascript:void(0)" class="button btn-buy" @click='hideMask'></a>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import $ from 'n-zepto'
import mui from 'mui'
import router from '@/router'


export default {
    name: 'submit',
    data() {
        return {
            isExpress: true,
            address: {},
            remark: '',
            isShowMask: false,
            isLoading:true
        }
    },
    methods: {
        addAddress() {
            router.push({
                name: 'EditAddress'
            })
        },
        selectAddress() {
            router.push({
                name: 'Address'
            })
        },
        hideMask() {
            $("div[class='xucun_content']").hide();
            var body = document.getElementsByTagName("body");
            $('#mask').hide()
            this.isShowMask = false;
            $(document).unbind("touchmove");
        },
        coverDiv() {
            var procbg = $('#mask')[0] //div
            procbg.style.background = "#000000";
            procbg.style.width = "100%";
            procbg.style.height = "100%";
            procbg.style.position = "fixed";
            procbg.style.top = "0";
            procbg.style.left = "0";
            procbg.style.zIndex = "500";
            procbg.style.opacity = "0.8";
            procbg.style.filter = "Alpha(opacity=70)";
            $('#mask').show();


            $(document).bind("touchmove", function(e) {
                e.preventDefault();
            });
        },
        toPay($event) {
            if ($($event.currentTarget).hasClass('disabled')) {
                mui.toast('')
            } else {
                mui.toast('')
                this.isShowMask = true;
                this.coverDiv();
            }

        },
        payment() {
            mui.toast('123')
            var params = {
                buyNum: this.product.buyNum,
                productId: this.product.productId,
                remark: this.remark
            }
            if (this.isExpress) {
                params.addrId = this.address.addrId
            }
            var skus = [];
            var skukeys = this.product.skuKeys;
            for (var i = 0; i < skukeys.length; i++) {
                var sku = {
                    "skuValues": skukeys[i].skuValues[this.skuIndexs[i]].id,
                    "id": skukeys[i].id
                }
                skus.push(sku);
            }
            if (skus.length > 0) params.skus = JSON.stringify(skus);

            console.log(JSON.stringify(params))

            this.$http.post('m/account/orderheader/toPay', params).then(res => {
                console.log(res)
                if (res.body.result === 'success') {
                    var appId = res.body.data.appId;
                    var getWxOpenIdUrl = res.body.data.getWxOpenIdUrl;
                    var state = res.body.data.orderNumber;
                    window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" + appId + "&redirect_uri=" + getWxOpenIdUrl + "&response_type=code&scope=snsapi_base&state=" + state + "#wechat_redirect"

                } else {
                    this.hideMask();
                    mui.alert(res.body.message)
                }
            })
        }
    },
    filters: {

    },
    computed: {
        product() {
            return this.$store.state.productToSubmit;
        },
        skuIndexs() {
            return this.$store.state.skuToSubmit;
        },
        picUrl() {
            var picUrls = this.product.picUrls ? this.product.picUrls.split(";;") : '';
            return picUrls[0];
        },
        totalAmt() {
            return this.product.buyNum * this.product.defaultPrice
        },
        totalAmtWithExpress() {
            if (this.isExpress && this.address.expressFee) {
                // return this.address.expressFee ? this.totalAmt : this.totalAmt + this.address.expressFee
                return this.totalAmt + this.address.expressFee
            } else {
                return this.totalAmt
            }
        }
    },
    mounted: function() {

    },
    created: function() {
        this.$http.get('m/account/userAddress/spa/getAddress', {
            params: {
                addrId: this.$route.query.addrId
            }
        }).then(
            res => {
                if (res) {
                    console.log(res)
                    if (res.body) this.address = res.body;

                }
                this.isLoading = false;
            },
            res => {
                if (res) {
                    if (res.status == 401) {
                        location.href = '/m/login?successUrl=' + encodeURIComponent(window.location.href);
                    }
                }
                 this.isLoading = false;
            })
    },
    components: {

    }
}

</script>
<style type="text/css" scoped="" lang="scss">
/*  */


/*  */

.slide-fade-enter-active {
    transition: all .5s ease;
}

.slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.slide-fade-enter,
.slide-fade-leave-to {
    transform: translateY(300px);
    opacity: 1;
}

.actionsheet-spec {
    top: auto;
    bottom: 0;
}

</style>
</style>



